import { JSON, FaceTransform, ImageTransform } from './classes/messages_classes';



//Face tracking
const FACE_TRACK_CONTROLLER_GAME_OBJECT_NAME = "AR-Renderer-Face-Track-Controller";

export function OnFaceFound(unityInstance) {
    unityInstance.SendMessage(FACE_TRACK_CONTROLLER_GAME_OBJECT_NAME, 'StartTracking');
}

export function OnFaceTracking(unityInstance, position, rotation, scale, cameraFOV, faceMeshData) {
    SendFaceTransform(unityInstance, position, rotation, scale, cameraFOV, faceMeshData);
}

export function OnFaceLost(unityInstance) {
    unityInstance.SendMessage(FACE_TRACK_CONTROLLER_GAME_OBJECT_NAME, 'StopTracking');
}

function SendFaceTransform(unityInstance, position, rotation, scale, cameraFOV, faceMeshData) {
    let faceTransform = new FaceTransform(position, rotation, scale, cameraFOV, faceMeshData);

    if (faceTransform.isValid()) {
        if (unityInstance) {
            unityInstance.SendMessage(FACE_TRACK_CONTROLLER_GAME_OBJECT_NAME, "Tracking", faceTransform.toJson());
        }
    } else {
        throw 'Face transform is invalid!';
    }
}
///



