import * as AR_Renderer from "@/AR-Renderer/AR-Renderer";

//#region "MindArThree setup"
const SCANNING_OVERLAY = "#scanningOverlay";
const LOADING_OVERLAY = "#loadingOverlay";
const WARM_UP_TOLERANCE = 5;
const MISS_TO_TOLERANCE = 10;
const FILTER_MIN_CF = 0.001;
const FILTER_BETA = 1000;
//#endregion


var renderer;
var scene;
var camera;

var isTargetVisible = false;

export async function OnUnityInstanceCreated(mindArContainer, targets, callback) {

    let mindAr;
    let faceMesh;

    if (targets) {
        mindAr = MindArImageThree(targets, mindArContainer);
    } else {
        mindAr = MindArFaceThree(mindArContainer);
        faceMesh = mindAr.addFaceMesh();
    }

    mindAr.addAnchor(0);

    renderer = mindAr.renderer;
    scene = mindAr.scene;
    camera = mindAr.camera;

    const start = async () => {
        await mindAr.start();
        callback();
        renderer.setAnimationLoop(() => {
            renderer.render(scene, camera);
            CheckTargetStatus(mindAr, faceMesh?.geometry?.positions);
        });
    }

    setTimeout(() => {
        start();
    }, 2000);
}

function CheckTargetStatus(mindAr, faceMesh) {
    let aspect = mindAr.container.offsetWidth / mindAr.container.offsetHeight;

    let cameraFOV = aspect < (4 / 3) ? mindAr.camera.fov : mindAr.camera.fov * (4 / 3) / aspect;
    let sceneAnchor = mindAr.anchors[0];
    
    if (!sceneAnchor.group.visible) {
        if (isTargetVisible) {
            isTargetVisible = false;

            if (faceMesh) {
                AR_Renderer.onFaceLost();
            } else {
                AR_Renderer.onImageLost();
            }
        }
    } else {
        let matrixElements = sceneAnchor.group.matrix.elements
        if (!isTargetVisible) {
            isTargetVisible = true;

            if (faceMesh) {
                AR_Renderer.onFaceFound();
            } else {
                AR_Renderer.onImageFound();
            }
        }

        if (faceMesh) {
            AR_Renderer.onFaceTracking(matrixElements, cameraFOV, faceMesh);
        } else {
            AR_Renderer.onImageTracking(matrixElements, cameraFOV);
        }
    }
}

function MindArImageThree(trackableImage, container) {
    return new window.MINDAR.IMAGE.MindARThree({
        container: container,
        imageTargetSrc: trackableImage,
        uiScanning: SCANNING_OVERLAY,
        uiLoading: LOADING_OVERLAY,
        
        warmupTolerance: WARM_UP_TOLERANCE,
        missTolerance: MISS_TO_TOLERANCE,
        filterMinCF: FILTER_MIN_CF,
        filterBeta: FILTER_BETA,
    });
}

function MindArFaceThree(container) {
    return new window.MINDAR.FACE.MindARThree({
        container: container,
        videoWidth: 1280,
        videoHeight:720
    });
}