class Vector3 {

	constructor(x = 0, y = 0, z = 0) {
		Vector3.prototype.isVector3 = true;
		this.x = x;
		this.y = y;
		this.z = z;
	}

	set(x, y, z) {
		this.x = x;
		this.y = y;
		this.z = z;

		return this;
	}

	magnitude() {
		return Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
	}

	toJson() {
		return `{ "x": ${this.x}, "y": ${this.y}, "z": ${this.z} }`
	}
}

export { Vector3 };