import { Vector3 } from '../math/Vector3.js';
import { Quaternion } from '../math/Quaternion';

class JSON {

	constructor() {
		JSON.prototype.isJSON = true;
	}
	toJson() {}
	isValid() {}
}

class FaceTransform extends JSON {

	constructor(position, rotation, scale, camerFOV = 60, faceMeshData = []) {
		super();
		FaceTransform.prototype.isFaceTransform = true;

		if (!position) {
			position = new Vector3();
		}
		if (!rotation) {
			position = new Quaternion();
		}
		if (!scale) {
			position = new Vector3();
		}

		this.position = position;
		this.rotation = rotation;
		this.scale = scale;
		this.camerFOV = camerFOV;
		this.faceMeshData = faceMeshData;
	}

	toJson() {
		const pos = this.position.toJson();
		const rot = this.rotation.toJson();
		const sc = this.scale.toJson();

		let temp = this.faceMeshData.join();
		return `{
					"position": ${pos}, 
					"rotation": ${rot}, 
					"scale": ${sc}, 
					"camerFOV": ${this.camerFOV}, 
					"facemesh": "${temp}"
				}`;
	}

	isValid() {
		if (this.position && this.rotation && this.scale && this.camerFOV && this.faceMeshData) {
			return true;
		} else {
			return false;
		}
	}
}

class ImageTransform extends JSON {

	constructor(position, rotation, scale, camerFOV = 60) {
		super();
		ImageTransform.prototype.isImageTransform = true;

		if (!position) {
			position = new Vector3();
		}
		if (!rotation) {
			position = new Quaternion();
		}
		if (!scale) {
			position = new Vector3();
		}

		this.position = position;
		this.rotation = rotation;
		this.scale = scale;
		this.camerFOV = camerFOV;
	}

	toJson() {
		const pos = this.position.toJson();
		const rot = this.rotation.toJson();
		const sc = this.scale.toJson();

		return `{
					"position": ${pos}, 
					"rotation": ${rot}, 
					"scale": ${sc}, 
					"camerFOV": ${this.camerFOV} 
				}`;
	}

	isValid() {
		if (this.position && this.rotation && this.scale && this.camerFOV) {
			return true;
		} else {
			return false;
		}
	}
}

class CameraTransform extends JSON {

	constructor(position, rotation) {
		super();
		CameraTransform.prototype.isCameraTransform = true;

		if (!position) {
			position = new Vector3();
		}
		if (!rotation) {
			position = new Vector3();
		}
		
		this.position = position;
		this.rotation = rotation;
	}

	toJson() {
		const pos = this.position.toJson();
		const rot = this.rotation.toJson();

		return `{
					"position": ${pos}, 
					"rotation": ${rot}, 
				}`;
	}

	isValid() {
		if (this.position && this.rotation) {
			return true;
		} else {
			return false;
		}
	}
}

export { JSON, FaceTransform, ImageTransform, CameraTransform };