<template>
    <div id="app" >
        <div class="control" @click="CalculateTargets()"></div>
        <div v-show="!ShowUnityPlayer" class="baseContainer" ref="baseContainerVideo">
            <video ref="video" :class="Flipped"></video>
            <canvas ref="photo" v-show="false"></canvas>
        </div>
        <div v-show="true" class="baseContainer">
            <canvas id="unity-canvas"></canvas>
            <div id="container" ref="container" :class="Flipped"></div>
        </div>
        
        <div v-if="ShowLoading" class="loadingIndicator">
            <div class="loader"></div>
        </div>
    </div>
</template>

<script>

    import * as AR_Renderer from "@/AR-Renderer/AR-Renderer";
    import { GenerateTarget } from "@/scripts/TargetGenerator";
    import { OnUnityInstanceCreated } from "@/scripts/index"
    import tracking_modes from "@/AR-Renderer/tracking_modes";

    export default {
        name: 'App',
        data() {
            return {
                targets: undefined,
                TRACKING_MODE: "{{{TRACKING_MODE}}}",
                //TRACKING_MODE: "face",
                UNITY_LOADED: false,
                ShowLoading: false,
                ShowUnityPlayer: false,
            };
        },
        computed: {
            Flipped() {
                return this.GetTrackingMode() === 'FACE' ? "fliped": "";
            }
        },
        created() {
            window.onunload = function() {
                fetch("/quit").catch((err) => {
                    throw err;
                });
            }
        },
        mounted() {
            this.Initialization();
        },
        methods: {
            async Initialization() {
                this.LoadUnityPlayer((unityInstance) => {
                    if (unityInstance) {
                        this.RunVideo();
                        this.UNITY_LOADED = true;
                        if (this.GetTrackingMode() === 'FACE' || this.GetTrackingMode() === 'IMAGE') {
                            this.CalculateTargets();
                        }
                    }
                });
            },
            Resize() {
                let video = this.$refs.video;
                let container = this.$refs.baseContainerVideo;

                if (!video) return;

                let vw, vh;
                const videoRatio = video.videoWidth / video.videoHeight;

                const containerRatio = container.clientWidth / container.clientHeight;
                if (videoRatio > containerRatio) {
                    vh = container.clientHeight;
                    vw = vh * videoRatio;
                } else {
                    vw = container.clientWidth;
                    vh = vw / videoRatio;
                }

                video.style.top = (-(vh - container.clientHeight) / 2) + "px";
                video.style.left = (-(vw - container.clientWidth) / 2) + "px";
                video.style.width = vw + "px";
                video.style.height = vh + "px";
            },
            async RunVideo() {
                let video = this.$refs.video;
                let container = this.$refs.baseContainerVideo;

                video.setAttribute('autoplay', '');
                video.setAttribute('muted', '');
                video.setAttribute('playsinline', '');
                video.setAttribute('ref', 'video');
                video.style.position = 'absolute'
                video.style.top = '0px'
                video.style.left = '0px'
                video.style.zIndex = '-2'
                container.appendChild(video);

                if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
                    return;
                }

                navigator.mediaDevices.getUserMedia({
                    audio: false, video: {
                        facingMode: (this.GetTrackingMode() === 'FACE' ? 'face' : 'environment'),
                    }
                }).then((stream) => {
                    video.addEventListener('loadedmetadata', () => {
                        video.setAttribute('width', video.videoWidth + "px");
                        video.setAttribute('height', video.videoHeight + "px");
                        window.addEventListener('resize', this.Resize());
                    });
                    video.srcObject = stream;
                }).catch((err) => {
                    console.log("getUserMedia error", err);
                });
            },
            async LoadUnityPlayer(callback) {
                let canvasUnity = document.querySelector("#unity-canvas");

                AR_Renderer.Init(canvasUnity, async (unityInstance) => {
                    callback(unityInstance);
                })
            },
            GetTrackingMode() {
                let trackingMode = this.TRACKING_MODE.toLowerCase();
                return tracking_modes[trackingMode];
            },
            ShowLoadingIndicator(state) {
                this.ShowLoading = state;
            },
            async CalculateTargets() {
                if (this.targets === undefined && this.UNITY_LOADED) {
                    let mode = this.GetTrackingMode();

                    switch (mode) {
                        case tracking_modes.image:
                            this.ShowLoadingIndicator(true);
                            this.targets = await GenerateTarget(['./target.png']);
                            break;
                        case tracking_modes.surface:
                            this.ShowLoadingIndicator(true);
                            var video = this.$refs.video;
                            var photo = this.$refs.photo;

                            photo.width = 240;
                            photo.height = video.videoHeight / video.videoWidth * photo.width;

                            photo.getContext('2d').drawImage(video, 0, 0, photo.width, photo.height);
                            var image_data_url = photo.toDataURL('image/jpeg');

                            this.targets = await GenerateTarget([image_data_url]);
                            break;
                        default:
                            this.targets = null;
                            break;
                    }

                    let mindArContainer = this.$refs.container;
                    if (this.targets === null) {
                        import("mind-ar/dist/mindar-face-three.prod.js").then(async () => {
                            OnUnityInstanceCreated(mindArContainer, undefined, () => {
                                this.ShowUnityPlayer = true;
                                this.ShowLoadingIndicator(false);
                                this.Resize();
                            });
                        });
                    } else {
                        import("mind-ar/dist/mindar-image-three.prod.js").then(async () => {
                            OnUnityInstanceCreated(mindArContainer, this.targets, () => {
                                this.ShowUnityPlayer = true;
                                this.ShowLoadingIndicator(false);
                                this.Resize();
                            });
                        });
                    }
                }
            },


            OnError(errorMessage) {
                return console.error(errorMessage);
            },
        }
    }
</script>

<style>
    @import '@/css/styles.css';
    @import 'purecss/build/pure.css';
    #container {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
    }

    .debug {
        position: fixed;
        height: 200px;
        width: 100%;
        color: black;
        background-color: palevioletred;
        z-index: 99;
    }

    .fliped {
        transform: scale(-1, 1);
    }

    #unity-canvas {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
    }

    .baseContainer {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 5;
    }

    .loadingIndicator {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 9999;
    }

    .video {
        position: fixed;
        right: 0;
        bottom: 0;
        min-width: 100%;
        min-height: 100%;
        transform: translateX(calc((100% - 100vw) / 2));
    }

    .loader {
        border: 16px solid #f3f3f3;/* Light grey */
        border-top: 16px solid #3498db; /* Blue */
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 2s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }

    .control {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 999;
    }
</style>
